$primary-color: #060b35;
$secondary-color: #616070;
$theme-color: #00acdc;
$white-color: #fff;
$card-border: 1px solid #f3f3f3;
$card-width: 200px;
$card-right: 48px;
$card-bottom: 46px;
$lheight-lg: 18px;
$lheight-md: 16px;
$lheight-sm: 14px;
$fweight-bold: 700;
$fweight-medium: 500;
$fweight-light: 400;
$fsize-lg: 16px;
$fsize-md: 14px;
$fsize-sm: 12px;
$fsize-xs: 10px;
$star-size: 16px;
$star-distance: 4px;
$star-count: 4;
$star-inactive: url("../images/star.svg") center no-repeat;
$star-active: url("../images/star-active.svg") center no-repeat;
$transition-duration: 0.3s;
